        :prop="number"
        :rules="[
          { required: true, message: '手机号不能为空', trigger: 'blur'},
          { validator(rule, value, callback, source, options) {
              var errors = [];
              if (!/^[a-z0-9]+$/.test(value)) {
                callback('手机号必须为数字值....');
              } 
            callback(errors);
  
            }}
        ]">
        <Input v-model = "telphone"></Input>
      </FormItem> -->
<!--       <FormItem
        :prop="number"
        :rules="[
        { type: 'number', required: true, message: '请填写数字', trigger: 'blur'},
        { type: 'number', message: '输入数字',trigger: 'blur' }]">
            <Input v-model = "number"></Input>
      </FormItem>
 -->      <!-- <FormItem
        :prop="email"
        :rules="[
          { required: true, message: '邮箱不能为空', trigger: 'blur' },
          { type: 'email', message: '邮箱格式不正确', trigger: 'blur' }]">
          <Input :value.sync = "number"></Input>
      </FormItem> -->

        <div>
        <Input v-model="telphone" placeholder="请输入电话号码" style="width: 300px"></Input>
        <p class="notice" v-if="true && telphone === '' ">请输入电话号码</p>
        <p class="notice" v-if="telphone != '' && (!telphonetest(telphone))">请输入电话号码</p>
        </div>

        <div>
        <Input v-model="number" placeholder="请输入数字" style="width: 300px"></Input>
        <p class="notice" v-if="true && number === '' ">请输入数字</p>
        <p class="notice" v-if="number != '' && (!numbertest(number))">请输入数字</p>
        </div>

        <div>
        <Input v-model="email" placeholder="请输入邮箱" style="width: 300px"></Input>
        <p class="notice" v-if="true && email === '' ">请输入邮箱</p>
        <p class="notice" v-if="email != '' && (!emailtest(email))">请输入邮箱</p>
        </div>

        <div>
        <Input v-model="chinese" placeholder="请输入中文" style="width: 300px"></Input>
        <p class="notice" v-if="true && chinese === '' ">请输入中文</p>
        <p class="notice" v-if="chinese != '' && (!chinesetest(chinese))">请输入中文</p>
        </div>

        <div>
        <Input v-model="english" placeholder="请输入英文" style="width: 300px"></Input>
        <p class="notice" v-if="true && english === '' ">请输入英文</p>
        <p class="notice" v-if="english != '' && (!englishtest(english))">请输入英文</p>
        </div>

        <div>
        <Input v-model="weburl" placeholder="请输入网址" style="width: 300px"></Input>
        <p class="notice" v-if="true && weburl === '' ">请输入网址</p>
        <p class="notice" v-if="weburl != '' && (!weburltest(weburl))">请输入网址</p>
        </div>

        <div>
        <Input v-model="idcard" placeholder="请输入身份证" style="width: 300px"></Input>
        <p class="notice" v-if="true && idcard === '' ">请输入身份证</p>
        <p class="notice" v-if="idcard != '' && (!idcardtest(idcard))">请输入身份证</p>
        </div>

        <!-- {{number}} -->
        <!-- {{numbertest(number)}} -->

    </div>
</template>

<script type="text/ecmascript-6">
import iview from 'iview'
export default {
  name: 'test',
  data () {
    return {
      telphone: '',
      number: '',
      email: '',
      chinese: '',
      english: '',
      weburl: '',
      idcard: ''
    }
  },
  methods: {
    numbertest (string) {
        var reg = /^[0-9]+\.?[0-9]*$/
        return reg.test(string)
    },
    telphonetest (string) {
        var reg = /^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/
        return reg.test(string)
    },
    chinesetest (string) {
        var reg = /^[\u4e00-\u9fa5]{0,}$/
        return reg.test(string)
    },
    englishtest (string) {
        var reg = /^[A-Za-z0-9]+$/
        return reg.test(string)
    },
    weburltest (string) {
        var reg = /^[a-zA-z]+\:\/\/[^\s]*$/
        return reg.test(string)
    },
    idcardtest (string) {
        var reg = /^([0-9]){18}(x|X)?$/
        return reg.test(string)
    },
    emailtest (string) {
        var reg = /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/
        return reg.test(string)
    }
  },

}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.test
  padding-top 200px
</style>


